.product-card {
  color: inherit;
  text-decoration: none;
}


.product-card h3 {
  margin: 25px 0 0;
  font-size: 16px;
  font-weight: 500;
  transition: color $easy;

  @media (max-width: $mobile) {
    margin-top: 20px;
  }
}

.product-card span {
  font-size: 14px;
  line-height: 18px;
  font-weight: 700;
}

.product-card:hover,
.product-card:active,
.product-card:focus {
  h3 {
    color: $gold;
  }

  span {
    text-decoration: underline;
  }
}
