.intro {
  position: relative;

  font-family: $garamond;
  color: $white;
  font-size: 38px;
  line-height: 130%;
  font-weight: 400;

  @media (max-width: $mobile) {
    font-size: 34px;
  }

  &--catalog {
    font-size: 22px;
  }
}

.intro h2 {
  @include title;

  font-size: 45px;
  text-transform: uppercase;

  @media (max-width: $mobile) {
    font-size: 38px;
  }
}

.intro p {
  margin: 0;
  font-style: italic;
}

.intro__img-wrapper {
  min-height: 261px;
  background-color: $gold;
  overflow: hidden;

  img {
    min-height: 261px;
    object-fit: cover;
  }
}

.intro__txt {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;

  display: flex;
  padding: 0 50px;
  flex-direction: column;
  justify-content: center;
}
